.featured {
    height: 100vh;
    position: relative;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .info {
        width: 25%;
        position: absolute;
        padding-left: 50px;
        bottom: 150px;
        color: white;
        display: flex;
        flex-direction: column;
        img{
            width: 400px;
            box-shadow: 20px 10px 50px black;

        }

        .desc {
            margin: 20px 0px;
        }

        .buttons {
            display: flex;

            button {
                padding: 10px 20px;
                border: none;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: 500;
                margin-right: 10px;
                cursor: pointer;

                &.play {
                    background-color: white;
                    color: var(--main-color);
                }

                &.more-info {
                    background-color: gray;
                    color: white;
                }

                span {
                    margin-left: 5px;
                }
            }
        }
    }
}

